/*==== text color & bg color ====*/
/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  position: relative;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

.section-padding {
  padding-block: 120px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 100px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 80px 0;
  }
}

section,
.section {
  position: relative;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1410px;
  }
}
/**
 * Utility CSS.
 */
/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
button.scroll-to-top {
  background-color: var(--color-secondary);
  height: 0px;
  width: 0px;
  overflow: hidden;
  border-radius: 50px;
  color: transparent;
  clear: both;
  visibility: hidden;
  position: fixed;
  cursor: pointer;
  display: block;
  border: none;
  right: 50px;
  bottom: 75px;
  font-size: 0px;
  outline: 0 !important;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}
button.scroll-to-top.style-2 {
  position: inherit;
  right: auto;
  bottom: auto;
  margin-left: 12px;
}
button.scroll-to-top svg {
  color: var(--color-white);
}

button.scroll-to-top.show {
  display: block;
  background-color: var(--color-secondary);
  right: 25px;
  bottom: 50px;
  height: 40px;
  width: 40px;
  visibility: visible;
  box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.25);
}
button.scroll-to-top.show:hover svg {
  animation: toBottomFromTop 0.7s forwards;
}
button.scroll-to-top.show svg {
  color: var(--color-white);
}

button.scroll-to-top.show:active {
  box-shadow: 0px 4px 8px 2px rgba(0, 0, 0, 0.25);
}

.item-social {
  position: relative;
  z-index: 1;
}
.item-social ul {
  gap: 10px;
}
.item-social ul li a {
  height: 36px;
  width: 36px;
  border: 1px solid var(--color-blue-100);
  border-radius: 36px;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
}
.item-social ul li a svg {
  color: var(--color-body);
  transition: all 0.4s ease-in-out;
}
.item-social ul li a:hover {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}
.item-social ul li a:hover svg {
  color: var(--color-white);
  animation: toBottomFromTop 0.7s forwards;
}
.item-social ul li.text {
  color: var(--color-black);
  font-size: 18px;
  font-weight: 600;
  margin-right: 6px;
}

/**
 * Components: Input.
 */
.input-group {
  width: 100%;
  position: relative;
  display: block;
}
@media only screen and (min-width: 321px) {
  .input-group {
    display: flex;
  }
}
.input-group::before {
  content: url(../../media/shape/sms-2.svg);
  position: absolute;
  top: 16px;
  left: 20px;
  z-index: 4;
}
.input-group .form-control {
  border-radius: 28px !important;
  border: 1px solid var(--color-blue-100);
  background-color: var(--color-white);
  height: 56px;
  width: 100%;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-black);
  padding: 0px 0px 0px 55px;
}
.input-group .form-control:focus {
  outline: 0;
  box-shadow: none;
  border-color: var(--color-black);
}
.input-group .form-control::placeholder {
  color: var(--color-base);
}
.input-group .btn-outline-secondary {
  border: 0;
  color: var(--color-primary);
  background-color: var(--color-secondary);
  border-radius: 44px !important;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  width: 44px;
  transition: all 0.4s ease-in-out;
  z-index: 4;
  margin-top: 10px;
}
@media only screen and (min-width: 321px) {
  .input-group .btn-outline-secondary {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 6px;
    margin-top: 0;
  }
}
.input-group .btn-outline-secondary svg {
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}
.input-group .btn-outline-secondary:hover {
  background-color: var(--color-primary);
  color: var(--color-black);
}
.input-group .btn-outline-secondary:hover svg {
  animation: headShake 1.5s;
  color: var(--color-white);
}
.input-group .btn-outline-secondary:focus {
  outline: 0;
  box-shadow: none;
}

.brand-wrap {
  padding-bottom: 100px;
}
.brand-wrap.style-2 {
  background-color: #EDFBFA;
  padding: 100px 0;
}
.brand-wrap.style-3 {
  padding: 100px 0;
}
.brand-wrap.style-4 {
  padding: 100px 0;
  background-color: var(--color-gray-9);
}

.brand {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
  .brand {
    gap: 40px;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media only screen and (min-width: 992px) {
  .brand {
    grid-template-columns: repeat(7, 1fr);
    gap: 60px;
  }
}
.brand li img {
  filter: grayscale(100%);
  opacity: 50%;
  transition: all 0.4s ease-in-out;
}
.brand li img:hover {
  filter: grayscale(0);
  opacity: 1;
}

@media only screen and (min-width: 1200px) {
  .rt-brand-wrap {
    position: static;
  }
}
.rt-brand-wrap:hover .button-navigation .swiper-button-prev {
  left: -10px;
  opacity: 1;
  visibility: visible;
}
.rt-brand-wrap:hover .button-navigation .swiper-button-next {
  right: -10px;
  opacity: 1;
  visibility: visible;
}
.rt-brand-wrap .button-navigation .swiper-button-next, .rt-brand-wrap .button-navigation .swiper-button-prev {
  height: 40px;
  width: 40px;
  border-radius: 40px;
  border: 0;
