}
.rt-brand-wrap .button-navigation .swiper-button-next:hover, .rt-brand-wrap .button-navigation .swiper-button-prev:hover {
  background-color: var(--color-secondary);
}
.rt-brand-wrap .button-navigation .swiper-button-next:hover svg, .rt-brand-wrap .button-navigation .swiper-button-prev:hover svg {
  color: var(--color-white);
}
.rt-brand-wrap.style-2 {
  padding: 100px 0;
}

.rt-brand-box {
  border-radius: var(--border-radius-xl);
  background-color: var(--color-white);
  padding: 30px 0;
}
.rt-brand-box:hover .rt-brand-img img {
  animation: toBottomFromTop 0.7s forwards;
}
.rt-brand-box .text {
  font-size: 16px;
  color: var(--color-black);
  font-weight: 600;
}
@media only screen and (min-width: 768px) {
  .rt-brand-box .text {
    font-size: 18px;
  }
}
.rt-brand-box .text span {
  font-weight: 400;
}
.rt-brand-box.style-2 {
  border-radius: 20px;
  border: 1px solid #FFF;
  background-color: #F6F6F6;
  padding: 30px 0 23px;
}
.rt-brand-box.style-2 .rt-brand-img {
  background-color: var(--color-white);
