}

.rt-brand-img {
  height: 120px;
  width: 120px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 120px;
  border: 1px solid var(--color-gray-line);
  margin-bottom: 20px;
  overflow: hidden;
}
.rt-brand-img img {
  transition: all 0.4s ease-in-out;
}

/**
 * Components: Blog.
