  color: var(--color-body);
}
.rt-main-menu .menu .menu-item .menu-link::before {
  height: 3px;
  width: 0;
  content: "";
  background-color: var(--color-primary);
  position: absolute;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link:hover {
  color: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link:hover::before {
  opacity: 1;
  width: 100%;
}

.rt-main-menu .menu .menu-item:hover.menu-item-has-children::after {
  border-color: var(--color-primary);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu {
  position: absolute;
  top: 100%;
  background: #ffffff;
  min-width: 260px;
  padding: 15px 12px 13px 12px;
  list-style: none;
  margin: 0;
  box-shadow: 0 13px 48px 0 rgba(0, 0, 0, 0.15);
  visibility: hidden;
  opacity: 0;
  z-index: 9;
  transform-origin: top center;
  transform: scaleY(0) translateZ(100px);
  transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item {
  margin-bottom: 0px;
  opacity: 0;
  transition: all 0.4s;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu {
  font-size: 16px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 500;
  overflow: hidden;
  font-family: var(--font-title);
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: 500ms;
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu .overlay {
  display: block;
  position: absolute;
  width: 100%;
  height: 0%;
  top: auto;
  left: 0%;
  bottom: 0%;
  background-color: var(--color-primary);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out;
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover {
  color: var(--color-white);
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover .overlay {
  height: 100%;
  top: 0%;
  bottom: auto;
  visibility: visible;
  opacity: 1;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu::before {
  position: absolute;
  top: 50%;
  right: 12px;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f061";
  font-size: 14px;
  color: var(--color-white);
  transform: translateY(-50%) scale(0);
  transition: 500ms;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover::before {
  transform: translateY(-50%) scale(1);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover::after {
  width: 100%;
}

.rt-main-menu .menu .menu-item:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1) translateZ(0px);
}

.rt-main-menu .menu .menu-item:hover > .sub-menu .menu-item {
  opacity: 1;
}

